import { Link } from "gatsby";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import React from "react";
import "../assets/sass/main.sass";
import Header from "../components/Header";

const Quest = ({ pageContext, location }) => {
  const { scene } = pageContext;
  console.log(location);
  if (scene) {
    return (
      <>
        <Header />
        <div className="scene">
          <GatsbyImage
            className="scene-background"
            image={getImage(scene.Background.localFile)}
            alt="background"
          />
          <GatsbyImage
            className="scene-person"
            image={getImage(scene.Person.localFile)}
            alt="person"
          />
          <div className="scene-controls-box">
            <p className="scene-person-name">{scene.PersonName}</p>
            <p className="scene-text">{scene.text}</p>
            <div className="scene-buttons">
              {scene.Buttons.map((button) => (
                <Link
                  to={`${location.pathname}/${button.Scene.id}`}
                  key={button.text}
                  className="scene-buttons_button">
                  {button.Text}
                </Link>
              ))}
            </div>
          </div>
        </div>
      </>
    );
  }
  return <></>;
};

export default Quest;
